import Link from 'next/link'
import { useRouter } from 'next/router'

const menuStyle = {
  height: '60px',
  color: '#a8a8a8',
  borderTop: '1px solid #dedede',
  backgroundColor: 'white',
  position: 'fixed',
  bottom: 0
}

export default function Menu(props) {
  const router = useRouter()
  const activeMenu = (id) => {
    console.log(id)
    return (router.query.case === id) ? 'menuitem active' : 'menuitem'
  }
  console.log(router)
  return (
    <div className="wrapper" style={menuStyle}>
      <div className="container menubar">
        <Link href='/?case=1'>
          <a className={activeMenu('1')}>Case 1</a>
        </Link>
        <Link href='/?case=2'>
          <a className={activeMenu('2')}>Case 2</a>
        </Link>
        <Link href='/?case=3'>
          <a className={activeMenu('3')}>Case 3</a>
        </Link>
        <Link href='/?case=4'>
          <a className={activeMenu('4')}>Case 4</a>
        </Link>
      </div>
      <style jsx>{`
        .menubar {
          align-items: center;
          padding: 0 14px;
          justify-content: space-around;
        }
        .menuitem {
          width: 80px;
          height: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          color: #00aaef;
          cursor: pointer;
        }
        .active {
          color: rgb(168, 168, 168);
        }
      `}</style>
    </div>
  )
}